import dispatches from "../sql/dispatchData.json";

const getInitialState = () => {
  const columns = [
    { id: "dispatch_id", name: "Dispatch ID", selector: (row) => row.dispatch_id, sortable: true },
    { id: "contract_id",name: "Contract ID", selector: (row) => row.contract_id, sortable: true },
    { id: "road_name",name: "Road Name", selector: (row) => row.road_name, sortable: true },
    { id: "start_m",name: "Start (m)", selector: (row) => row.start_m, sortable: true },
    { id: "end_m",name: "End (m)", selector: (row) => row.end_m, sortable: true },
    { id: "donrc",name: "ONRC", selector: (row) => row.onrc, sortable: true },
    { id: "days_since",name: "Days Since", selector: (row) => row.days_since, sortable: true },
    {
      name: "Asset Type",
      selector: (row) => row.asset_type_desc,
      sortable: true,
    },
    { id: "fault_desc",name: "Fault Type", selector: (row) => row.fault_desc, sortable: true },
    { id: "priority_desc",name: "Priority", selector: (row) => row.priority_desc, sortable: true },
    { id: "call_type_desc",name: "Call Type", selector: (row) => row.call_type_desc, sortable: true },
    {
      id: "programme_category_desc",name: "Programme Category",
      selector: (row) => row.programme_category_desc,
      sortable: true,
    },
    {
      id: "score",name: "Score",
      selector: (row) => row.score.toFixed(4),
      sortable: true,
    },
  ];

  dispatches.forEach((d) => {
    d.score = 1
  });

  const initialState = {
    data: dispatches,
    columns: columns,
    controllers: {
      onrc: {
        title: "ONRC",
        values: [...new Set(dispatches.map((d) => d.onrc).sort())],
        controls: {},
      },
      days_since: {
        title: "Days Since",
        values: [...new Set(dispatches.map((d) => d.days_since).sort())],
        controls: {},
      },
      asset_type_desc: {
        title: "Asset Type",
        values: [...new Set(dispatches.map((d) => d.asset_type_desc).sort())],
        controls: {},
      },
      fault_type: {
        title: "Fault Type",
        values: [...new Set(dispatches.map((d) => d.fault_desc).sort())],
        controls: {},
      },
      priority_desc: {
        title: "Priority",
        values: [...new Set(dispatches.map((d) => d.priority_desc).sort())],
        controls: {},
      },
      call_type_desc: {
        title: "Call Type",
        values: [...new Set(dispatches.map((d) => d.call_type_desc).sort())],
        controls: {},
      },
      programme_category_desc: {
        title: "Programme",
        values: [...new Set(dispatches.map((d) => d.programme_category_desc).sort())],
        controls: {},
      },
    }
  };
  return initialState;
}

export default getInitialState;
