import React from 'react'
import HeaderBar from './HeaderBar'
import { Container, Tabs, Tab } from 'react-bootstrap'
import { useAppContext } from '../context/AppContext'
import Datatables from './Datatables'
import Controls from './Controls';

export default function Main() {
  const context = useAppContext();
  const { state, dispatch } = context;
  
  return (
    <>
    <HeaderBar/>
    <Container style={{ marginTop: "0.5rem"}}>
      <Tabs defaultActiveKey="controls">
      <Tab eventKey="controls" title="Controllers">
        <Controls />
        </Tab>
        <Tab eventKey="table" title="Data Tables">
          <Datatables data={state.data} columns={state.columns} asset_types={state.controllers.asset_type_desc.values} />
        </Tab>        
        </Tabs>
    </Container>
    </>
  )
}
