
const reducer = (state, action) => {
  const { type, payload } = action;

  switch (type) {
    case "update_deep_field":
      let group = state[payload.group];
      group = { ...group, [payload.field]: payload.value };
      return {
        ...state,
        [payload.group]: group,
      };

    case "add_controller":
      const existingControls = state.controllers[payload.field].controls;

      state.controllers[payload.field].controls = {
        ...existingControls,
        [payload.option]: payload.value,
      };
      return { ...state };

    case "update_controller_value":
      state.controllers[payload.field].controls[payload.option] = payload.value;

      return { ...state, data: calculateScores(state.data, state.controllers)};

    case "remove_controller":
      delete state.controllers[payload.field].controls[payload.option]

      return { ...state, data: calculateScores(state.data, state.controllers) };
  }
};

const calculateScores = (data, controllers) => {
  data.forEach((d) => {
    let score = [1];
    Object.keys(controllers).forEach((control) => {
      let value = d[control];

      const multiplier = controllers[control].controls[value] || "1";
      score.push(parseFloat(multiplier));
    });
    d.score = score.reduce((a, b) => a * b);
  });

  return data;
};



export default reducer;
