import { useState } from "react";
import DataTable from "react-data-table-component";
import { Container, Form } from "react-bootstrap";
import percentRank from "percentile-rank";

const Datatables = (props) => {
  const { data, columns, asset_types } = props;

  const [asset, setAsset] = useState("All");

  const filteredItems = data.filter((row) =>
    asset === "All" ? true : row.asset_type_desc === asset
  );
  
  const paginationComponentOptions = {
    rowsPerPageText: "Rows per page",
    rangeSeparatorText: "of",
    selectAllRowsItem: false,
    selectAllRowsItemText: "",
  };

  const allScores = filteredItems.map(item=>parseFloat(item.score))

  filteredItems.forEach((d) => {
    d.rank = percentRank(allScores, d.score)
  });


  return (
    <Container style={{ marginTop: "0.5rem", marginBottom: "0.5rem" }}>
      <div style={{ marginBottom: "0.5rem"}}>
        <span style={{ fontSize: "1.2rem", fontWeight: "bold"}}>
          Select Asset Type: 
        </span>
        <span>
          <Form.Select
            style={{ width: "20%" , marginTop: "0.4rem"}}
            value={asset}
            onChange={(e) => setAsset(e.target.value)}
          >
            <option value={"All"}>All</option>
            {asset_types.map((m) => {
              return (
                <option key={`asset_type-${m}`} value={m}>
                  {m}
                </option>
              );
            })}
          </Form.Select>
        </span>
      </div>
      <div>
        <DataTable
          columns={[...columns, {
            id: "rank",name: "Rank",
            selector: (row) => ((row.rank) * 100).toFixed(2),
            sortable: true,
          }]}
          data={[...filteredItems]}
          pagination
          paginationComponentOptions={paginationComponentOptions}
          keyField="Dispatch ID"
          paginationPerPage={15}
        />
      </div>
    </Container>
  );
};

export default Datatables;
